body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.comingSoon {
  font-family: 'Kaushan Script', cursive;
  font-size: 5rem;
  color: #000000;
  /* margin-top: 30rem; */
}

.logo img {
  height: auto;
  width: 10rem;
}

/* Add a black background color to the top navigation */

.navigation {
  background-color: #1c2550;
  /* background-color: rgba(51, 51, 51, 0.5); */
  overflow: hidden;
  font-weight: bold;
  text-align: center;
}

/* Style the links inside the navigation bar */

.navigation span {
  float: left;
  display: block;
  /* color: #f2f2f2; */
  /* color: #283673; */
  /* color: #fbde37; */
  color: #ffffff;
  text-align: center;
  padding: 14px 16px;
  /* text-decoration: none; */
  font-size: 17px;
}

/* Change the color of links on hover */

.navigation span:hover {
  background-color: #FCF751;
  color: #000000;
  cursor: pointer;
}

/* Add an active class to highlight the current page */

.navigation span.active {
  /* background-color: #4CAF50; */
  /* background-color: #fbde37; */
  /* color: white; */
  color: #FCF751;
}

.navigation span.active:hover {
  color: #FCF751;
  background-color: #1c2550;
}
/* Hide the link that should open and close the navigation on small screens */

.navigation .icon {
  display: none;
}

/* media queries BEGIN */

/* When the screen is less than 600 pixels wide, hide all links, except for the first one ("Home"). Show the link that contains should open and close the topnav (.icon) */

@media screen and (max-width: 900px) {
  .navigation span {
    padding: 14px 16px;
  }
  .navigation span:not(:first-child) {
    display: none;
  }
  .navigation span.icon {
    float: right;
    display: block;
  }
}

/* The "responsive" class is added to the navigation with JavaScript when the user clicks on the icon. This class makes the topnav look good on small screens (display the links vertically instead of horizontally) */

@media screen and (max-width: 900px) {
  .navigation.responsive {
    position: relative;
  }
  .navigation.responsive span.icon {
    position: absolute;
    right: 0;
    top: 0;
  }
  .navigation.responsive span {
    float: none;
    display: block;
    text-align: left;
  }
}
